.grid{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.card{
  margin: 10px;
  width: 500px;
  padding: 10px;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    display: flex;
    margin-top: 1.5rem;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: 0.25rem;
}

.card h5{
  font-size: 1.5rem;
  display: flex;
  justify-content: flex-start;
  width: 100%;
}

.video{
  width: 100%;
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;

}